* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  outline: none;
  border: none; }

a, li {
  display: inline-block; }

.header {
  width: 1920px;
  position: relative; }
  .header__block {
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 43px;
    padding-right: 20px;
    background: #FAF5F5;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3); }
    .header__block-logo a::before {
      position: absolute;
      top: 10px;
      content: "";
      width: 80px;
      height: 80px;
      background: #4f4949;
      border-radius: 50%; }
    .header__block-logo a::after {
      position: absolute;
      top: 36px;
      left: 117px;
      content: "";
      width: 119px;
      height: 29px;
      background: #4f4949; }
    .header__block-input {
      width: 567px;
      position: relative; }
      .header__block-input input {
        width: 100%;
        border-radius: 30px;
        background: #FFFFFF;
        border: 1px solid #ADACAC;
        font-size: 16px;
        padding: 10px 25px 10px 15px; }
      .header__block-input a img {
        position: absolute;
        right: 11px;
        top: 10px;
        z-index: 2;
        color: #4f4949; }
